<template>
    <div>
        <div class="header clearfix">
            <h1 class="display-1 mb-3">InstaZipper</h1>
            <div class="border-top divider float-left"></div>
            <div class="border-top divider float-right"></div>
        </div>

        <div class="row mt-3">
            <div class="col align-self-center">
                <div class="form-group">
                    <input
                        type="text"
                        class="form-control form-control-lg"
                        placeholder="@instagram"
                        v-on:keyup="usernameKeyUp" :value="result">
                </div>
            </div>
        </div>
        <process class="process" v-show="processing" :progress="progress" :total="igCount">{{message}}</process>
    </div>
</template>

<script>
import Velocity from 'velocity-animate'
import Process from './Process'
import axios from 'axios'
import JSZip from 'jszip'
import FileSaver from 'file-saver'
export default {
  components: {
    Process
  },
  data: function () {
    return {
      result: null,
      processing: false,
      url: 'https://www.instagram.com/graphql/query/',
      igResponse: null,
      igCount: 0,
      queryHash: '472f257a40c653c64c666ce877d59d2b',
      variables: {
        'id': '5823115431',
        'first': 200
      },

      progress: 0,
      total: 0,
      zip: null,
      message: 'Progress'
    }
  },
  methods: {
    usernameKeyUp: function (event) {
      if (event.keyCode === 13) {
        this.$data.processing = true
        this.$data.result = Math.random()

        Velocity(document.getElementsByClassName('divider'), {
          width: '50%'
        }, {
          duration: '1000'
        })

        Velocity(event.target, {
          transform: 'translateY(100px)',
          opacity: 0
        }, {
          queue: 'input'
        })
        Velocity(document.getElementsByClassName('process')[0], {
          transform: 'translateY(-90px)',
          opacity: 1
        }, {
          queue: 'input'
        })

        this.fetchData(event.target.value)
      }
    },
    fetchData: function (id) {
      let that = this
      this.$data.variables.id = id
      axios.get(this.$data.url, {
        params: {
          'query_hash': this.$data.queryHash,
          'variables': JSON.stringify(this.$data.variables)
        }})
        .then(function (response) {
          that.$data.igResponse = response.data.data.user.edge_owner_to_timeline_media.edges
          that.archiver()
        })
        .catch(function (error) {
          console.log(error)
        })
    },
    archiver: function () {
      this.$data.zip = JSZip()
      let promises = []
      let that = this

      this.$data.igResponse.forEach(element => {
        if (element.node.__typename === 'GraphImage') {
          that.$data.igCount++
          promises.push(
            axios.get(element.node.display_url, {
              responseType: 'arraybuffer'
            })
          )
        }
      })

      Promise.all(promises)
        .then(axios.spread((...args) => {
          args.forEach(function (arg) {
            if (arg.status === 200) {
              let arrayBuffer = arg.data
              let url = arg.request.responseURL
              let fileName = url.split('/').pop()
              that.$data.zip.file(fileName, arrayBuffer, {binary: true})

              that.$data.progress++
            }
          })
        })).then(function () {
          that.$data.message = 'Success'
          that.$data.zip.generateAsync({type: 'blob'})
            .then(function (blob) {
              FileSaver.saveAs(blob, 'InstaZipper.zip')
            })
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}
</script>

<style>
input.form-control {
  z-index: -1;
}

.divider {
  width: 0%;
}

.process {
  opacity: 0;
}
</style>



