<template>
  <div class="row">
     <div class="col align-self-center">
       <transition-group>
        <p :key="'msg-1'"
          class="display-1"
          v-bind:class="{
            'text-primary': progress < total || total === 0,
            'text-success': progress === total && total > 0 }"><slot/></p>
        <p :key="'msg-2'" class="display-4">{{progress}}/{{total}}</p>
       </transition-group>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    progress: null,
    total: null
  },
  data: function () {
    return {
      message: null
    }
  }
}
</script>

<style>
div.row {
  transition: .5s;
}

display-1 {
  transition: .5s;
}
</style>



